<template>
   <transition name="mod">
    <div class="modal" v-if="show_hiden">
   
      <div class="modal_content" v-show="show_hiden">
        <img src="http://cdn.shangrongfintech.com/static/images/tanchuangbg1.png" alt="">
<!-- <slot name="modal_main"></slot> -->
<span class="close" @click="close">&times;</span>
<form>
  <div class="input-bank">
  <label for="user">姓名</label>
  <input id="user" type="text" maxlength="6" placeholder="请输入姓名">
    </div>
  <div class="input-bank">
  <label for="tel">手机</label>
  <input id="tel" type="text" maxlength="11" placeholder="请输入电话">
  <span class="send_code">发送验证码</span>
    </div>
  <div class="input-bank">
  <label for="code">验证码</label>
  <input id="code" type="text" maxlength="6" placeholder="请输入验证码">
  </div>
</form>
    </div>

  </div>
    </transition>  
</template>
<script>
export default {
  name: "Dialog",
  data() {
    return {
      show_hiden:true,
    };
  },
  methods:{
    close(){
      this.show_hiden= false;
    }
  }
};
</script>
<style lang="scss">

.mod-enter-active {
  transition: all .5s ease;
}
.mod-leave-active {
  transition: all .5s ease;
}
.mod-enter {
  transform: translateY(-500px);
  opacity: 0;
}
.mod-leave-active {
  transform: translateY(-500px);
    opacity: 0;
}


.modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  transition: all 0.5s;
  .modal_content {
     max-width:768px;
    // min-width:320px;
    width: 80%;
    height: 70%;
    background: #fff;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    box-sizing: border-box;
    border-radius: 5px 5px 5px 5px;
    .close{
      position: absolute;
     top: 0;
    right: 4%;
      font-size: 32px;
      color:#fff;
      font-weight: 100;

    }
    img{
      width:100%;
    }
  }
}
form{
  padding: 0 20px;
  margin:0 auto;
  background-color: #fff;
}
.input-bank{
  position: relative;
  text-align: left;
  height: 55px;
  line-height: 55px;
  border-bottom:1px solid #eee;
  .send_code{
  line-height: 22px;
    border: 1px solid #53e8ee;
    border-radius: 8px;
    text-align: center;
    color: #53e8ee;
    padding: 0 3px;
    position: absolute;
    top:14px;
    right: 0;
    font-size: 12px;

  }
  input{
    padding:0;
    width: 40%;
    border:none;
    height:30px;
    outline: none;
    margin:0 0 0 55px;
  }
  label{
    position: absolute;
  }
}
</style>



